div.saaqv-main-widget-error {
    padding: 0 !important;
    .saa-qv-notes-error {
        padding: 10px;
        background-color: $error-red;
    }
    .saaqv-top-block {
        margin: 10px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .saaqv-top-block-line {
            display: flex;
            justify-content: space-between;
            width: 100%;
        }
    }
}
div.saaqv-main-widget {
    .saaqv-top-block {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        .saaqv-top-block-line {
            display: flex;
            justify-content: space-between;
            width: 100%;
        }
    }
    p {
        font-size: 20px;
    }
    padding: 10px;
    border-radius: 3px;
    margin-bottom: 10px;
    max-width: 320px;
    border: 1px solid lightgray;
    .saa-qv-header-ip {
        margin-right: 2px;
    }
    .saa-input-button-inactive {
        &:hover {
            color: $orange;
        }
    }
    .fa-thumbs-down,
    .fa-thumbs-up {
        width: 15px;
        height: 15px;
    }
    .saa-qv-notes-likes {
        border-top: 1px rgb(189, 189, 189) solid;
        padding-top: 5px;
        margin-top: 10px;
        .saa-central-main {
            max-width: fit-content;
        }
        .saa-qv-buttons {
            margin-bottom: 5px;
            display: flex;
            justify-content: space-between;
            .saa-dislike {
                svg {
                    margin-bottom: -5px !important;
                }
            }
            .fa-layers {
                &:after {
                    content: "";
                    background: #ff3737;
                    width: 5px;
                    position: absolute;
                    height: 5px;
                    border-radius: 50%;
                    top: -3.3px;
                    left: 13.9px;
                    pointer-events: none;
                    opacity: 0;
                    transition: ease opacity 0.2s;
                    transition-delay: 0s;
                }
                &.filled:after {
                    opacity: 1;
                    transition-delay: 0.1s;
                }
            }
        }

        .saa-qv-inputs {
            .fade-enter-active {
                transition: all 0.4s ease;
            }
            .fade-leave-active {
                transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
            }
            .fade-enter,
            .fade-leave-to {
                transform: translateX(10px);
                opacity: 0;
            }
            .saa-central-commit {
                height: auto;
                min-height: 40px;
            }
            .saa-central-url {
                width: 100%;
                margin-bottom: 2px;
                padding-right: 22px;
            }
            .source-link-block {
                position: relative;

                .source-link-href,
                .sk-fading-circle,
                .input-updated {
                    position: absolute;
                    right: 5px;
                    top: 5px;
                }
                .sk-fading-circle,
                .input-updated {
                    top: 8px;
                }
                .sk-fading-circle {
                    margin: 0;
                    width: 16px;
                    height: 16px;
                }
            }
            .saa-qv-commit-block {
                position: relative;
                .sk-fading-circle,
                .input-updated {
                    position: absolute;
                    right: 5px;
                    top: 8px;
                }
                .sk-fading-circle {
                    margin: 0;
                    width: 16px;
                    height: 16px;
                }
            }
            .saa-qv-url {
                width: 100%;
                margin-bottom: 2px;
                padding-right: 22px;
            }
        }
        .saa-tooltip-box {
            position: unset;
            &.saa-connect-acc {
                position: relative;
            }
            a {
                border-radius: 50%;
            }
            .saa-tooltip.saa-need-approval {
                top: auto;
                left: auto;
                margin-left: -4%;
            }
        }
    }
    .gate-spinner {
        margin-left: 5px;
        color: rgb(189, 189, 189);
    }

    .alert {
        text-align: center;
        padding: 5px;
        border-radius: 3px;
    }

    .badge {
        // color: white;
        padding: 3px;
        border-radius: 3px;
    }

    .alert-dagner {
        color: white;
        background-color: $orange;
    }

    .saa-qv-tag-box {
        display: flex;
        text-align: left !important;
    }

    .saa-qv-tag {
        padding: 2px 6px;
        margin: 1px;
        border-radius: 5px;
        text-decoration: none;
    }

    .fbm {
        @include snippets-box($white, $blue);
    }
    .fba {
        @include snippets-box($white, $orange);
    }
    .amz {
        @include snippets-box($black-color, $light-orange);
    }
    .saa-tooltip {
        color: $black-color;
    }
    .saa-data-source {
        font-size: 10px;
        line-height: 125%;
        color: #565959;
    }
}
