.saa-main {
    .snowman-block {
        display: none;
    }
}
.happy-new-year {
    background-size: 110%;
    max-width: 550px;
    .snowman-block {
        display: block;
        position: relative;
        .snowman {
            position: absolute;
            display: block;
            height: 40px;
            width: 37px;
            bottom: -8px;
            cursor: pointer;
        }
    }
}
