.saa-onboarding-target {
    box-shadow: 0 0 0 99999px rgba(0,0,0,.4);
    position: relative;
    border-radius: 5px;
    input {
        background-color: white !important;
    }
    padding-right: 5px;
    padding-left: 5px;
}
@for $i from 0 through 1000 {
    .custom-position-#{$i} {
        transform: translate(-380px, -#{$i}px) !important;
    }
}
.saa-main-block:has(.saa-onboarding-overlay) {
    border: 11px solid rgba(236, 236, 236, 1);
    border-radius: 10px;
    .saa-footer {
        .saa-sheets-button {
            .saa-lb {
                filter: unset !important;
            }
            .saa-rb {
                filter: unset !important;
            }
        }
    }
    #api-flot-placeholder {
        filter: unset !important;
    }
}
.saa-main-block:not(.saa-onboarding-target).saa-main-block:not(.saa-onboarding-overlay) {
    .saa-footer {
        .saa-sheets-button {
            .saa-lb {
                filter: unset;
            }
            .saa-rb {
                filter: unset;
            }
        }
    }
}
.saa-main-block:has(.saa-onboarding-target),.saa-main-block:has(.saa-onboarding-overlay) {
    .saa-calc-input {
        background-color: transparent !important;
    }
    .saa-input {
        input {
            background-color: transparent !important;
        }
    }
    #api-flot-placeholder {
        filter: brightness(0.6);
    }
    #v-step-8:not(.saa-onboarding-target) {
        .saa-sheets-button {
            .saa-lb {
                filter: brightness(0.6);
            }
            .saa-rb {
                filter: brightness(0.6);
            }
        }
    }
}
.p-5 {
    padding: 5px;
}
.pt-1 {
    padding-top: 1px;
}
.pl-1 {
    padding-left: 1px;
}
.v-step {
    font-size: 14px;
    max-width: 367px !important;
    inset: unset !important;
    width: 360px !important;
    background: rgba(232, 232, 232, 0.9) !important;
    color: black !important;
    .v-step__arrow {
        display: none;
    }
    .v-step__buttons {
        .v-step__button {
            min-width: 90px;
            max-height: 26px;
            color: black !important;
            border-color: black !important;
        }
    }
    .close-btn {
        position: absolute;
        right: 10px;
        top: 8px;
        cursor: pointer;
    }
    .v-step__content {
        margin-top: 1rem !important;
        p {
            text-align: start;
        }
        .custom-step-title {
            margin-bottom: 10px;
            display: block;
            text-align: start;
        }
    }
}
.saa-onboarding-icon {
    font-size: 14px;
    margin-right: 4px;
    margin-top: 1px;
    color: rgba(255, 155, 5, 1);
    cursor: pointer;
}
.display-none {
    display: none !important;
}
.v-step__button {
    box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.08) !important;
    border: none !important;
    padding: 0px 20px 0px 20px !important;
    border-radius: 7px !important;
    &.v-step__button-next,&.v-step__button-stop {
        background-color: rgba(255, 216, 20, 1) !important;
    }
    &.v-step__button-previous,&.v-step__button-skip {
        background-color: rgba(255, 255, 255, 1) !important;
        border-color: 1px solid rgba(211, 211, 211, 1) !important;
    }
}