.saa-profit-and-fees {
    .saa-pb-10 {
        padding-bottom: 10px;
    }
    .saa-show-tooltip {
        min-width: 170px;
    }
    .star-tooltip {
        white-space: normal;
        margin-left: 88px;
    }
    .saa-bsr-tooltip-row {
        justify-content: space-between;
        display: flex;
        flex: 1;
    }
    .saa-bsr-tooltip-col {
        display: flex;
        flex: 1;
        flex-direction: column;
        padding: 0;
        border-right: 1px solid $light-gray;
    }
    .saa-bsr-tooltip-col:last-child {
        border: 0;
    }
    .saa-bsr-tooltip-item {
        padding: 0.3rem 0.5rem;
        width: 100%;
        flex: 1 0 1.5rem;
        text-align: center;
        white-space: nowrap;
    }
    .saa-bsr-tooltip-header {
        border-bottom: 1px solid $light-gray;
    }
    .saa-block {
        width: 100%;
        min-height: 152px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 0 10px;
    }

    hr {
        width: 100%;
        margin: -0.25rem 0 0.25rem;
    }

    .saa-background {
        .saa-main-block:not(.saa-onboarding-target) {
            background-color: $white-background;
        }
        flex: 1 1 calc(50% - 20px);
        border-radius: 5px;
        padding: 0.25rem 0.5rem;
        margin-bottom: 9px;
    }
    .saa-dropdown-fees {
        margin-left: -30px;
        width: 60px;
    }

    input.saa-3pl-input {
        width: 40px;
        font-size: 12px;
        height: 25px;
        margin-top: -0.2rem;
        margin-right: -7px;
        text-align: center;
    }
    input.saa-input {
        width: 40px;
        font-size: 12px;
        height: 25px;
        margin-top: -0.2rem;
        margin-right: -7px;
        text-align: center;
    }
    input {
        -moz-appearance:textfield;
    }
    .saa-circle-arrow {
        float: right;
    }
    .saa-star {
        position: absolute;
        margin-left: 0.5rem;
        margin-top: -0.4rem;
    }
    .saa-fees-row {
        justify-content: space-between;
        display: flex;
    }
    .saa-fees-col {
        flex: 1 1 50%;
    }
    .saa-fees-col-right {
        margin: 0 0.5rem 0 0.75rem;
    }

    i.icon-down-open-mini::before {
        margin-left: -0.2rem;
        margin-right: -0.2rem;
    }

    .saa-hr-invisible {
        border-color: $white-background;
    }

    .saa-fees-item {
        margin: 0 0.5rem 0 0;
        width: 100%;
        height: 1.7rem;
    }
    .saa-header-model-switch {
        margin-bottom: 0.25rem;
    }
    .saa-color-secondary {
        color: $dark-gray;
    }
    .ssa-text-strike {
        opacity: 0.5;
    }

    .saa-inbound-placement-tooltip {
        .saa-dropdown {
            width: 280px;
            margin-left: -140px;
        }
    }

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
    }

    .toolip-row {
        display: flex;
        justify-content: space-between;
    }
    .toolip-row > div {
        padding: 5px;
    }

    .saa-profit-row {
        justify-content: space-between;
        display: flex;
        flex: 1 1 calc(50% - 20px);
        .saa-show-tooltip {
            top: 65px;
        }
        .toolip-row {
            display: flex;
            // justify-content: space-between;
        }
        .toolip-row > div {
            padding: 5px;
            flex: 1 0 10%;
        }

        .toolip-row > div:first-child {
            flex: 0 0 55%;
        }

        .toolip-hr {
            margin: 5px 0;
        }

        .saa-profit-col {
            display: flex;
            flex: 1 1 40%;
            flex-direction: column;
            padding: 0 0.2rem 0 0;
        }

        .saa-profit-col-header {
            ul > li {
                list-style: none;
            }
        }
        .saa-profit-item {
            padding: 0.5rem 0.5rem;
            width: 100%;
            flex: 1 0 1.5rem;
            text-align: center;
            margin: 0px 0.2rem;
        }

        .saa-profit {
            font-size: 13px;
            font-weight: bold;
        }
        .saa-profit-item-header-left {
            margin: 0;
            padding: 0.3rem 0;
            text-align: left;
            font-weight: bold;
            .saa-tooltip {
                font-weight: normal;
            }
        }
        .saa-profit-item-header-center {
            text-align: center;
            font-weight: bold;
        }

        .tooltip-box {
            border-radius: 50%;
            position: relative;
        }
        .tooltip {
            position: absolute;
            z-index: 999;
            user-select: none;
            pointer-events: none;
            background-color: white;
            top: 70px;
            width: max-content;
            left: 50%;
            padding: 10px;
            border-radius: 3px;
            border: 1px solid lightgray;
            visibility: hidden;
            opacity: 0;
            transition: 0.25s;
        }

        .tooltip-box:hover .tooltip {
            visibility: visible;
            opacity: 1;
            transition: 0.25s;
        }

        .table {
            border-spacing: 4px;
        }
        th,
        td {
            text-align: center;
            vertical-align: middle;
        }
        tr > td:first-child {
            text-align: left;
            vertical-align: middle;
        }
    }
}
