.saa-calc {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    hr {
        width: 100%;
        margin: 0 0 5px 0;
    }

    .my-8 {
        margin: 8px 0px;
    }

    .saa-calc-title {
        text-align: center;
        margin-bottom: 4px;
    }

    .saa-calc-inputs {
        margin-top: 5px;
        margin-bottom: 5px;
        width: 100%;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(135px, 1fr));
        grid-row-gap: 5px;
        line-height: 25px;
        .saa-input-vat {
            padding-top: 4px;
            height: 22px;
            width: 40px;
            border-radius: 5px;
            font-size: 12px;
            text-align: center;
        }
        input.saa-calc-input {
            width: 100%;
            margin-bottom: 3px;
            border-radius: 5px;
            height: 34px;
            font-size: 14px;
            &.error{
                box-shadow: 0 0 0 3px rgba(255, 0, 0, 0.25), 0 1px 2px rgb(15 17 17 / 15%) inset;
            }
        }
        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            -webkit-appearance: none;
            margin: 0;
        }
        input[type=number] {
            -moz-appearance:textfield;
        }
        .saa-calc-block {
            min-width: 125px;
            width: auto;
            .saa-calc-line {
                display: flex;
                justify-content: space-around;
                align-items: baseline;
                &.saa-max-width {
                    max-width: 50px;
                }
                &.saa-width-100 {
                    width: 100%;
                    max-width: 170px;
                    position: relative;
                }
                &.saa-margin-left {
                    margin-left: 5px;
                }
                .saa-text {
                    font-size: 15px;
                }
                .saa-mx-5 {
                    margin: 0px 5px;
                }
            }
        }
        .saa-calc-block:last-child {
            margin-left: 10px;
        }
        .saa-input-title {
            padding: 0 0.25rem 0 0;
            .saa-header-model-switch {
                display: inline;
                .saa-dropdown-cog {
                    width: 150px;
                    left: 78px;
                }
            }
        }
        .saa-tooltip-price {
            .saa-show-tooltip {
                min-width: 138px;
                max-width: 250px;
                padding: 0;
                .saa-vat-item {
                    font-size: 12px;
                    line-height: 20px;
                    color: $dark-blue;
                    display: flex;
                    justify-content: space-between;
                    span:first-child {
                        margin-left: 5px;
                    }
                    span:last-child {
                        margin-right: 5px;
                    }
                }
                .saa-vat-item:hover {
                    background-color: $white-background;
                    color: $orange;
                    cursor: pointer;
                }
            }
        }
        .saa-show-tooltip {
            min-width: 220px;
            top: 35px;
            &.saa-show-tooltip-left {
                transform: translate(0, 0);
                left: 0;
            }
            .saa-cog-tooltip-line {
                display: flex;
                justify-content: space-between;
                align-items: center;
                &.saa-text-12 {
                    span:first-child {
                        font-size: 12px;
                    }
                }
            }
            .saa-margin-tooltip-bottom {
                margin-bottom: 4px;
            }
            .saa-cog-tooltip-row {
                justify-content: space-between;
                display: flex;
                flex: 1 1 0;
                .saa-cog-tooltip-col {
                    display: flex;
                    flex-direction: column;
                    flex: 1 1 0;
                    padding: 0;
                    border-right: 1px solid $light-gray;
                    &:last-child {
                        border-width: 0;
                        border-style: initial;
                        border-color: initial;
                        border-image: initial;
                    }
                    .saa-cog-tooltip-header {
                        border-bottom: 1px solid $light-gray;
                    }
                    .saa-cog-tooltip-item {
                        width: 100%;
                        text-align: center;
                        white-space: nowrap;
                        padding: 0.3rem 0.5rem;
                        flex: 1 0 1.5rem;
                        font-size: 12px;
                        &.mt-0 {
                            margin-top: 0px !important;
                        }
                    }
                    .saa-cog-tooltip-item:last-child {
                       margin-top: 5px;
                    }
                }
            }
        }
        .saa-salestax-item:hover {
            background-color: $white-background;
            margin: 0 -10px;
        }
        .saa-salestax-item {
            text-align: center;
        }
        .saa-calc-inputs-sales-tax {
            width: 30%;
            .a-color-secondary.saa-nowrap {
                color: $dark-blue !important;
            }
            .saa-dropdown-salestax {
                margin-left: -50px;
                width: fit-content;
                &::before {
                    left: 50px;
                }
            }
        }
        .saa-calc-inputs-vat {
            .a-color-secondary.saa-nowrap {
                color: $dark-blue !important;
            }
        }
    }

    .saa-calc-stats {
        span.a-size-small.a-color-secondary {
            display: flex;
            justify-content: space-between;
            align-items: baseline;
        }
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        .saa-price-arrow {
            line-height: 0 !important;
        }
        .saa-price-plus-selector {
            .saa-dropdown-price {
                width: 150px;
                margin-left: -75px;
            }
            .saa-dropdown-price-item {
                display: flex;
                justify-content: space-around;
                text-align: center;
                padding: 0 10px;
                cursor: pointer;
                span {
                    width: 50%;
                }
            }
            .saa-dropdown-price-item:hover {
                background-color: $white-background;
            }
            .saa-dropdown-price-container {
                margin: 0 -10px;
            }
            .saa-dropdown-price-header {
                display: flex;
                justify-content: space-between;
                text-align: center;
                border-bottom: 1px solid $light-gray;
            }
            .saa-dropdown-price-header > span {
                flex: 1 1 50%;
            }
        }
    }
    .calc-input-err-tooltip {
        padding: 7px;
    }
}
.saa-calc-stats {
    span.a-size-small.a-color-secondary {
        display: flex;
        align-items: baseline;
        flex-wrap: wrap;
        &>span {
            margin: 0 5px;
        }
    }
}
