.saa-keepa-graph {
    input.api-size[type="number"]::-webkit-outer-spin-button,
    input.api-size[type="number"]::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }

    .api-menu-item {
        margin-right: 5px;
        align-self: center;
    }

    .api-menu-item-right {
        margin-left: 5px;
    }

    .api-hint-div {
        z-index: 100;
        padding: 10px 15px;
        border-width: 1px;
        margin-top: 3px;
        border-radius: 5px;
        border-style: solid;
        border-color: $gray;
        background-color: $white;
    }

    .saa-graph-hint {
        z-index: 100;
        background-color: $white;
    }

    .legendLayer .background {
        fill: $transparent-gray;
    }

    .legendLayer tspan {
        font-size: 10px !important;
    }

    #api-legend-placeholder {
        font-size: 10px;
    }

    .flot-tick-label {
        font-size: 10px !important;
        font-family: "Amazon Ember", Arial, sans-serif;
    }

    #api-legend-ul {
        margin: auto;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    .api-legend-li {
        margin: 0 10px;
        padding: 0;
        font-size: 12px;
    }
    .saa-keepagraph-controls {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap-reverse;
    }
    div.saa-container {
        padding: 7px 10px;
        border-radius: 3px;
        border: 1px solid $light-gray;
        max-width: 550px;
    }
}
