.saa-free-container-login {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px;
    margin-bottom: 5px;
    border-radius: 3px;
    border: 1px solid $light-gray;
    max-width: 550px;
    transition: all 0.5s;
    .saa-free-login-image {
        min-width: 80px;
        min-height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 10px 0 10px 0;
        overflow: hidden;
        .saa-image {
            box-shadow: 1px 2px 4px grey;
            border-radius: 50%;
            padding-left: 5px;
            max-width: 100%;
            margin: 0 auto;
        }
    }
    .saa-free-welcome {
        margin: 0 0 8px 0;
        font-size: 16px;
        font-weight: 500;
    }
    .saa-free-text {
        margin: 0 0 10px 0;
        text-align: center;
        p:last-child {
            color: #555555 !important;
            font-size: 13px !important;
        }
    }
    .saa-free-text-footer {
        margin-bottom: 15px;
        display: flex;
        justify-content: center;
    }
    .saa-cta-btn {
        box-sizing: border-box;
        background-color: #FFD814;
        display: flex;
        -webkit-box-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        align-items: center;
        border: 1px transparent;
        border-radius: 7px;
        transition: border-color 0.4s ease, transform 0.4s ease, box-shadow 0.4s ease, -webkit-transform 0.4s ease;
        padding: 8px 16px;
        font-size: 12px;
        max-width: 200px;
        width: 120px;
        color: $cta-text-blue !important;
        align-self: center;
        font-weight: 400;
        line-height: 16px;
        letter-spacing: 0em;
        text-align: center;
        text-decoration: none;
        &:hover {
            background-color: #F7CA00;
        }
        &:active {
            background-color: #F4B135;
        }
    }
}
.saa-free-container {
    padding: 0px;
    margin-bottom: 5px;
    border-radius: 3px;
    border: 1px solid $light-gray;
    max-width: 550px;
    transition: all 0.5s;
    .saa-free-fetching {
        margin: 8px;
    }
    .saa-background {
        margin-left: auto !important;
        li:first-child {
            padding: 5px 5px 8px 5px;
        }
    }
    .a-size-base {
        margin-left: 4px;
    }
    .saa-fbm-badge {
        padding-top: 0;
        padding-bottom: 0;
    }
    .saa-free-tooltip {
        max-width: 225px;
    }
    hr {
        margin-bottom: 5px;
        margin-top: 5px;
        width: 97%;
    }
    .fade-enter-active {
        transition: opacity 0.5s;
    }
    .fade-enter,
    .fade-leave-to {
        opacity: 0;
    }
    .saa-free-offers-items {
        display: flex;
        margin-top: 10px;
        margin-bottom: 10px;
    }
    .saa-product-info-block {
        margin: 0 8px 0;
        .saa-block {
            align-items: baseline;
        }
    }
    .saa-product-info-left {
        li:first-child {
            margin-top: 0;
        }
    }
    .saa-product-info-right {
        li:first-child {
            margin-top: 0;
        }
    }
    .saa-free-header-block {
        display: flex;
        justify-content: space-between;
        margin: 8px 8px 5px 8px;

        .saa-free-product-mp {
            p {
                margin: 0;
            }
        }
        .saa-header-offers {
            .saa-pointer {
                margin-left: 2px;
            }
            .saa-dropdown {
                left: -208px;
            }
        }
        .api-menu-item {
            .saa-dropdown-search {
                left: 15px;
            }
        }
        .saa-header-gate {
            margin-top: 2px;
            margin-left: 2px;
        }
        .saa-product-info-block {
            margin: 8px;
        }
        .saa-free-items-left {
            padding-bottom: 2px;
            display: flex;
        }
        .saa-free-items {
            margin-left: 5px;
            display: flex;
            align-items: center;
            .api-menu-item {
                margin-right: 4px;
            }
        }
        .saa-free-items-rigth {
            padding-bottom: 2px;
        }
        .saa-bsr-tooltip-row {
            justify-content: space-between;
            display: flex;
            flex: 1;
        }
        .saa-bsr-tooltip-header {
            border-bottom: 1px solid $light-gray;
        }
        .saa-bsr-tooltip-col {
            display: flex;
            flex: 1;
            flex-direction: column;
            padding: 0;
            border-right: 1px solid $light-gray;
        }
        .saa-bsr-tooltip-col:last-child {
            border: 0;
        }
        .saa-bsr-tooltip-item {
            padding: 0.3rem 0.5rem;
            width: 100%;
            flex: 1 0 1.5rem;
            white-space: nowrap;
            text-align: center;
        }
        .saa-dropdown-button {
            height: 21px;
            cursor: pointer;
            display: inline-flex;
            .saa-tooltip-box {
                display: flex;
                align-items: center;
                margin: 0 8px 0 0;
                cursor: pointer;
            }
            .saa-tooltip {
                font-size: 12px;
                line-height: 15px;
                color: $black-color;
            }
            svg {
                margin: auto;
                path {
                    opacity: 1;
                    fill: $dark-blue !important;
                }
            }
            svg:hover path {
                fill: $orange !important;
            }
            path {
                transform: rotateX(180deg);
                transform-origin: center;
            }
            .close {
                transform: rotateX(0deg);
            }
            .saa-tooltip {
                width: max-content !important;
                margin: 12px 0 !important;
            }
        }
    }
    .saa-free-offers {
        .saa-dropdown {
            width: 710px;
        }
    }
    .saa-free-tooltip-display {
        span {
            display: block;
        }
        svg {
            display: block;
        }
        .saa-show-tooltip {
            top: 15px;
            max-width: 215px;
        }
        .saa-tooltip-pos {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 5px;
            margin-right: 2px;
            margin-left: 0px;
        }
    }
    .saa-tooltip-width {
        max-width: 215px;
    }
    .saa-fbm {
        @include snippets-box($white, $blue);
        border-radius: 3px;
        padding: 2px;
        margin-right: 10px;
        padding-left: 4px;
        padding-right: 4px;
    }
    .saa-fba {
        @include snippets-box($white, $orange);
        border-radius: 3px;
        padding: 2px;
        padding-left: 4px;
        padding-right: 4px;
    }
    .saa-free-notify-icons {
        display: flex;
        align-items: center;
        span {
            margin-left: 5px;
        }
        .saa-free-gate-pointer {
            cursor: pointer;
        }
        .saa-free-ip-margin {
            p {
                margin-bottom: 0;
            }
        }
        .saa-free-triangle {
            margin-top: 5px;
        }
        .saa-dislike {
            margin-top: 4px;
        }
        .saa-free-amazon {
            margin-top: 1px;
            svg {
                transform: scale(1.2);
            }
        }
        .saa-free-hands {
            display: contents;
            .saa-free-like {
                margin-bottom: 4px;
            }
        }
    }
}
