.saa-product-info-block {
    li {
        list-style-type: none;
        margin: 0.3rem 0;
    }
    .saa-block {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    hr {
        width: 100%;
        margin: 0 0 5px 0;
    }
    .saa-highlight-blue {
        margin-left: 0;
    }
    .saa-product-info-left {
        flex: 1 1 50%;
        min-width: 150px;
        padding-right: 0.5rem;
        font-size: 13px;

        .saa-bsr-tooltip-row {
            justify-content: space-between;
            display: flex;
            flex: 1;
        }

        .saa-free-product-bsr {
            display: flex;
            align-items: center;
            .saa-tooltip-container {
                display: flex;
                align-items: center;
            }
        }
        .saa-free-product-mp {
            p {
                margin: 0;
            }
        }
        .saa-bsr-tooltip-header {
            border-bottom: 1px solid $light-gray;
        }
        .saa-bsr-tooltip-col {
            display: flex;
            flex: 1;
            flex-direction: column;
            padding: 0;
            border-right: 1px solid $light-gray;
        }
        .saa-bsr-tooltip-col:last-child {
            border: 0;
        }

        .saa-bsr-tooltip-item {
            padding: 0.3rem 0.5rem;
            width: 100%;
            flex: 1 0 1.5rem;
            text-align: center;
            white-space: nowrap;
        }

        .saa-nowrap {
            overflow: hidden;
            text-overflow: ellipsis;
        }

        li > * {
            vertical-align: middle;
            max-width: initial;
        }

        #vgt-table {
            td {
                vertical-align: middle;
            }
            span li {
                margin: 0;
            }
            li > * {
                vertical-align: bottom;
            }
        }

        i {
            transition: 0.5s;
            border-radius: 50%;
        }

        .keepa-tooltip {
            width: 350px;
        }
        .tooltip-sales {
            width: 150px;
            left: 20px;
            .saa-highlight-green.saa-color-green {
                margin-left: 0;
            }
        }
        .saa-parent-asin-info {
            .saa-dropdown-parent-asin {
                margin-left: 0;
                transform: translate(-50%, 0px);
                width: fit-content;
            }
            div.saa-dropdown > li {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
        }
        .saa-sales-estimate {
            .saa-bsr-tooltip-row {
                justify-content: space-between;
                display: flex;
                flex: 1;
            }
            .saa-bsr-tooltip-header {
                border-bottom: 1px solid $light-gray;
            }
            .saa-bsr-tooltip-col {
                display: flex;
                flex: 1;
                flex-direction: column;
                padding: 0;
                border-right: 1px solid $light-gray;
            }
            .saa-bsr-tooltip-col:last-child {
                border: 0;
            }

            .saa-bsr-tooltip-item {
                padding: 0.3rem 0.5rem;
                width: 100%;
                flex: 1 0 1.5rem;
                text-align: center;
                white-space: nowrap;
            }

            .saa-pi-container {
                flex: 1 1 50%;
                min-width: 150px;
            }

            li > * {
                vertical-align: middle;
                max-width: initial;
            }

            i {
                transition: 0.5s;
                border-radius: 50%;
            }

            .keepa-tooltip {
                width: 350px;
            }
            .tooltip-sales {
                width: 250px;
            }
        }
    }

    .saa-product-info-right {
        flex: 1 1 50%;
        font-size: 13px;

        div.saa-background {
            background-color: $white-background;
            .saa-main-block:not([input]) {
                background-color: $white-background;
            }
            border-radius: 5px;
            margin-left: -0.5rem;
            padding: 0.25rem 0.5rem 0 0.5rem;
        }
        div.saa-background > li {
            margin: 0;
        }
        .saa-upc,
        .saa-info-ean {
            .saa-tooltip-box {
                display: inline;
            }
        }
        .saa-upc,
        .saa-info-ean,
        .saa-info-pq {
            color: $black-color !important;
            font-size: 13px !important;
            line-height: 20px !important;
            .saa-dropdown-upc {
                margin-left: -100px;
                width: 200px;
            }
        }
        .saa-info-pq {
            .saa-tooltip {
                white-space: initial;
            }
            .saa-pq-item {
                text-align: center;
                &:hover {
                    background-color: $white-background;
                }
                a:hover {
                    text-decoration: none;
                }
            }

            .saa-pq-row {
                display: flex;
                justify-content: space-between;
            }
            .saa-pq-col {
                flex: 1 1 auto;
            }
        }
        .saa-info-size {
            b {
                font-weight: 700;
                color: $black-color !important;
                font-size: 13px !important;
                line-height: 20px !important;
            }
            input.saa-size-input {
                width: 40px;
                font-size: 12px;
                height: 24px;
                text-align: right;
                margin: auto;
            }
            input::-webkit-outer-spin-button,
            input::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
            }

            .saa-bsr-tooltip-row {
                justify-content: space-between;
                display: flex;
                flex: 1;
            }
            .saa-bsr-tooltip-header {
                border-bottom: 1px solid $light-gray;
            }
            .saa-bsr-tooltip-col {
                display: flex;
                flex: 1;
                flex-direction: column;
                padding: 0;
                border-right: 1px solid $light-gray;
            }
            .saa-bsr-tooltip-col:last-child {
                border: 0;
            }

            .saa-bsr-tooltip-item {
                padding: 0.3rem 0.5rem;
                width: 100%;
                flex: 1 0 1.5rem;
                white-space: nowrap;
                text-align: center;
            }

            .saa-dropdown-size {
                margin-left: -115px;
                width: 230px;
            }

            .saa-tooltip-size {
                width: 350px;
            }
        }

        .saa-info-seller {
            i {
                margin: 0 -0.2rem;
            }
            .saa-seller-info {
                display: inline-block;
            }
        }
        .saa-offers-row {
            justify-content: space-between;
            display: flex;

            i.icon-down-open-mini::before {
                margin-left: -0.2rem;
                margin-right: -0.2rem;
            }

            i.icon-down-open-mini::before {
                margin-left: -0.2rem;
                margin-right: -0.2rem;
            }
            div.saa-pi-container {
                flex: 1 1 50%;
            }

            .saa-offers-col {
                flex: 1 1 auto;
            }

            div.saa-background {
                background-color: $white-background;
                border-radius: 5px;
                margin-left: -0.5rem;
                padding: 0.25rem 0.5rem 0 0.5rem;
            }
            div.saa-background > li {
                margin: 0;
            }

            .saa-vert-devider {
                background: $light-gray;
                width: 1px;
                margin-left: 0.5rem;
                margin-right: 0.5rem;
                margin-bottom: 5px;
            }

            .tooltip-box {
                border-radius: 50%;
                position: relative;
            }
            .tooltip {
                position: absolute;
                z-index: 100;
                user-select: none;
                pointer-events: none;
                background-color: $white;
                width: 200px;
                margin-top: -17px;
                top: 100%;
                left: 50%;
                padding: 10px;
                border-radius: 3px;
                border: 1px solid $light-gray;
                visibility: hidden;
                opacity: 0;
                transition: 0.25s;
                transition-delay: 0s;
            }
            .tooltip-fba-sales-est,
            .tooltip-fbm-sales-est {
                width: 350px;
                left: 40px;
                span.saa-color-red.saa-highlight-red,
                span.saa-color-green.saa-highlight-green {
                    margin-left: 0;
                    align-items: center;
                    display: inline-flex;
                    padding: 3px 5px;
                }
            }

            .tooltip-box:hover .tooltip {
                visibility: visible;
                opacity: 1;
                transition: 0.25s;
                transition-delay: 0.5s;
            }
            .saa-mws-error {
                display: flex;
                & > a {
                    margin-right: 5px;
                }
                & > span {
                    margin-bottom: 3px;
                }
            }
        }
    }
}
