.saa-central-main {
    max-width: 200px;
    font-size: 12px;
    .saa-central-btn-block {
        display: flex;
        justify-content: space-between;
        .saa-input-button-inactive {
            &:hover {
                color: $orange;
            }
        }
        .fa-thumbs-down,
        .fa-thumbs-up {
            width: 15px;
            height: 15px;
        }
        .fa-layers {
            margin: 3px;
            &:first-child {
                margin-left: 0;
            }
            &:last-child {
                margin-right: 0;
            }
        }
        .saa-central-uc-btn-block {
            .fa-layers {
                &:after {
                    content: "";
                    background: #ff3737;
                    width: 5px;
                    position: absolute;
                    height: 5px;
                    border-radius: 50%;
                    top: -3.3px;
                    left: 13.9px;
                    pointer-events: none;
                    opacity: 0;
                    transition: ease opacity 0.2s;
                    transition-delay: 0s;
                }
                &.filled:after {
                    opacity: 1;
                    transition-delay: 0.1s;
                }
            }
        }
    }
    .saa-central-uc-inputs {
        .fade-enter-active {
            transition: all 0.4s ease;
        }
        .fade-leave-active {
            transition: all 0.2s cubic-bezier(1, 0.5, 0.8, 1);
        }
        .fade-enter,
        .fade-leave-to {
            transform: translateX(10px);
            opacity: 0;
        }
        .saa-central-commit {
            height: auto;
            min-height: 40px;
        }
        .saa-central-url {
            width: 100%;
            margin-bottom: 2px;
            padding-right: 22px;
        }
        .source-link-block {
            position: relative;

            .source-link-href,
            .sk-fading-circle,
            .input-updated {
                position: absolute;
                right: 5px;
                top: 5px;
            }
            .sk-fading-circle {
                margin: 0;
                width: 16px;
                height: 16px;
            }
        }
        .saa-commit-input-block {
            position: relative;
            .sk-fading-circle,
            .input-updated {
                position: absolute;
                right: 5px;
                top: 5px;
            }
            .sk-fading-circle {
                margin: 0;
                width: 16px;
                height: 16px;
            }
        }
    }

    .saa-dislike {
        svg {
            margin-bottom: -5px !important;
        }
    }

    .saa-bsr-tooltip-row {
        justify-content: space-between;
        display: flex;
        flex: 1;
    }
    .saa-bsr-tooltip-header {
        border-bottom: 1px solid $light-gray;
    }
    .saa-bsr-tooltip-col {
        display: flex;
        flex: 1;
        flex-direction: column;
        padding: 0;
        border-right: 1px solid $light-gray;
    }
    .saa-bsr-tooltip-col:last-child {
        border: 0;
    }

    .saa-bsr-tooltip-item {
        padding: 0.3rem 0.5rem;
        width: 100%;
        flex: 1 0 1.5rem;
        text-align: center;
        white-space: nowrap;
    }

    .saa-dropdown-box {
        border-radius: 50%;
        position: relative;
        text-align: left;
    }
    .saa-dropdown {
        position: absolute;
        z-index: 1000;
        background-color: white;
        width: auto;
        margin-left: -100px;
        top: 25px;
        left: 50%;
        padding: 10px;
        border-radius: 3px;
        border: 1px solid lightgray;
        visibility: hidden;
        opacity: 0;
        transition: 0.2s;
        p {
            margin-bottom: 8px;
        }
    }
    .isOpen {
        visibility: visible;
        opacity: 1;
    }
}
