.saa-footer {
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;

    hr {
        width: 100%;
        margin-top: 5px;
        margin-bottom: 10px;
    }

    .saa-footer-links {
        display: flex;
        align-items: center;

        div > * {
            margin-right: 5px;
        }
        a {
            white-space: nowrap;
            vertical-align: sub;
        }

        .saa-new-image {
            position: absolute;
            margin-bottom: 28px;
            margin-left: 13px;
            height: auto;
            width: 25px;
        }
        .saa-sidepanel-button {
            padding-top: 2px;
        }
    }

    .saa-footer-inputs {
        flex-grow: 2;
        display: flex;
        padding: 0 2px 0 0;
        .saa-input {
            width: 100%;
            .saa-tooltip{
                left: 50%;
                overflow-wrap: break-word;
                word-wrap: break-word;
                -ms-hyphens: auto;
                -moz-hyphens: auto;
                -webkit-hyphens: auto;
                hyphens: auto;
            }
        }
        a {
            font-size: 12px;
            margin-left: -17px;
        }
        & > div {
            margin: 0 2px;
            position: relative;
            flex: 1 1 auto;
            & > input {
                position: absolute;
                width: 100%;
                display: block;
            }
            & > a {
                line-height: 24px;
                position: absolute;
                right: 5px;
            }
        }

        .saa-sl-input-padding {
            padding-right: 20px;
        }
        .saa-source-link-input,
        .saa-notes-input {
            width: 100%;
            line-height: 8px;
            font-size: 12px;
            height: 24px;
            margin: 0 2px;
        }
    }

    .saa-sheets-button {
        margin-left: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        align-content: center;

        .saa-button-red {
            background: $gradient-red !important;
        }

        svg {
            width: 16px;
            height: 10px;
        }
        i {
            width: 16px;
            height: 16px;
        }
        position: relative;
        z-index: 2;
        &:hover {
            .a-button {
                background: #f7ca00;
                border-color: #f2c200;
                box-shadow: 0 2px 5px 0 rgba(213, 217, 217, 0.5);
            }
        }

        .a-button {
            box-shadow: none;
            &.saa-lb {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-left: 10px;
                border-bottom-right-radius: 0;
                border-top-right-radius: 0;
                border-right: 0;
                svg {
                    width: 12px;
                    height: 12px;
                }
            }
            &.saa-rb {
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
                width: 25px;
                border-left: 0;
                margin-left: -3px;
                svg {
                    padding-top: 6px;
                }
            }
            .a-button-inner {
                display: contents;
            }
        }

        span {
            font-size: 12px;

            &.between-save-popup {
                opacity: 0.15;
                margin-left: 5px;
            }
        }
        .a-button-text {
            padding-right: 0 !important;
            padding-left: 3px !important;
        }
        .saa-sheets-button-dropdown {
            position: absolute;
            background: $white;
            right: 0;
            width: 200px;
            top: 23px;
            border: solid 1px $light-gray;
            border-radius: 3px;
            padding: 10px;
        }

        ul {
            margin: 0;

            li {
                list-style: none;

                &.new-spreadsheet {
                    margin-top: 10px;
                    padding-top: 5px;
                    border-top: solid 1px $light-gray;
                }

                a {
                    &.sheet-name {
                        list-style-position: inside;
                        white-space: nowrap;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        width: auto;
                        display: inline-block;
                        margin: -2px;
                    }
                    &.default {
                        font-weight: 600;
                    }

                    &.new-sheet-config {
                        font-size: 16px;
                    }

                    cursor: pointer;
                    text-decoration: none;
                    &:hover {
                        text-decoration: none;
                    }
                }

                span {
                    background: $white-background;
                    padding: 1px 8px;
                    border-radius: 8px;
                    font-size: 11px;
                    float: right;
                }
            }
        }
    }
}
