.attention {
    color: #B12704;
}
.halloween {
    position: absolute;
    display: block;
    height: 40px;
    width: 90px;
    left: 60%;
    bottom: 10px;
    cursor: default;
    &.bats {
        left: 55%;
        transform: scale(1.3);
        opacity: 0.5;
        pointer-events: none;
    }
    &.bats-mini {
        transform: scale(0.9);
        bottom: 0px;
        opacity: 0.5;
        pointer-events: none;
    }
    &.cat {
        transform: scale(1.3);
        bottom: 0px;
        left: -50px;
        cursor: pointer;
    }
}
.cursor-pointer {
    cursor: pointer;
}
.saa-bell-container {
    .btn-primary {
        font-weight: 400;
        font-size: 12px;
        background-color: #ebe460;
        background-image: linear-gradient(175deg, #ebe460, #ffb543);
        border: none;
        padding: 5px 10px;
        border-radius: 2px;
        color: #30364e;
    }
    .free-bell {
        padding: 0 !important;
        margin-left: 5px;
    }
    .free-bell-padding {
        padding-top: 2px;
    }
    .saa-bell {
        cursor: pointer;
        display: flex;
        padding-left: 5px;
        padding-top: 3px;
        justify-content: center;
        .saa-bell-value {
            line-height: 15px;
            position: absolute;
            font-size: 9px;
            color: #ffff;
        }
        .decrease {
            font-size: 7px;
        }
    }
    .saa-bell-dropdown {
        border-radius: 6px;
        padding: 10px;
        box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
        z-index: 99;
        position: absolute;
        right: auto;
        width: 300px;
        height: auto;
        background-color: #ffff;
        display: flex;
        flex-direction: column;
        .text-center {
            text-align: center;
            margin-bottom: 9px;
        }
        hr {
            margin-top: 6px;
            margin-bottom: 0px;
            width: 100%;
        }
        .bell-dropdown-header {
            font-weight: 400;
            font-size: 13px;
        }
        .bell-dropdown-body {
            .bell-empty-text {
                font-size: 10px;
                color: #8a8d93;
            }
            .bell-notifi {
                margin-top: 8px;
                .bell-dot {
                    position: absolute;
                    left: 4px;
                }
                .title {
                    font-weight: 500;
                    font-size: 13px !important;
                    margin-bottom: 6px;
                    line-height: 12px;
                    display: flex;
                    justify-content: space-between;
                    .days {
                        font-weight: 300;
                        font-size: 12px;
                        min-width: fit-content;
                    }
                    span {
                        color: $black-color !important;
                    }
                }
                .button {
                    margin-top: 4px;
                    margin-bottom: 12px;
                }
                .mb-8 {
                    margin-bottom: 8px;
                }
                hr {
                    margin-top: 6px;
                    margin-bottom: 6px;
                    width: 100%;
                }
            }
        }
        .bell-dropdown-footer {
            margin-left: auto;
            margin-bottom: 2px;
            button {
                margin-right: 2px;
            }
        }
    }
    /* we will explain what these classes do next! */
    .v-enter-active,
    .v-leave-active {
        transition: opacity 0.5s ease;
    }

    .v-enter-from,
    .v-leave-to {
        opacity: 0;
    }
}
.d-flex {
    display: flex;
}
.d-block {
    display: block;
}
.d-inherit {
    display: inherit;
}
.saa-main-block,
.saa-keepa-graph {
    background: $white;
}
.saa-main-block,
.saaqv-main-widget,
.saa-central-main {
    font-size: 13px;
    .saa-bold {
        font-weight: bold;
    }
    .saa-pointer {
        cursor: pointer;
    }
    .saa-shadow-gray {
        box-shadow: 0 0 10px $shadow-gray;
    }
    .saa-color-red {
        color: $red;
    }
    .saa-color-green {
        color: $green;
    }
    .saa-color-blue {
        color: $blue;
    }
    .saa-color-gray {
        color: $dark-gray;
    }
    .saa-color-light-gray {
        color: $light-gray;
    }

    .saa-tooltip-box {
        border-radius: 50%;
        position: relative;
    }
    .saa-tooltip-left-50 {
        .saa-show-tooltip {
            left: 50px;
        }
    }
    .saa-tooltip-left-58 {
        .saa-show-tooltip {
            left: 58%;
        }
    }
    .saa-tooltip-mb-4 {
        margin-bottom: 4px;
    }
    .saa-tooltip-left-30 {
        .saa-show-tooltip {
            left: 30px;
        }
    }
    .saa-tooltip-left-70 {
        .saa-show-tooltip {
            left: 70%;
        }
    }
    .saa-fbm {
        @include snippets-box($white, $blue);
        border-radius: 3px;
        padding: 2px;
        margin-right: 10px;
        padding-left: 4px;
        padding-right: 4px;
    }
    .saa-fba {
        @include snippets-box($white, $orange);
        border-radius: 3px;
        padding: 2px;
        padding-left: 4px;
        padding-right: 4px;
    }
    .saa-tooltip-left-60 {
        .saa-show-tooltip {
            left: 60%;
        }
    }
    .saa-tooltip-width {
        .saa-show-tooltip {
            min-width: max-content;
        }
    }
    .saa-tooltip-top-28 {
        .saa-show-tooltip {
            top: 28px !important;
        }
    }
    .saa-tooltip,
    .tooltip,
    .saa-show-tooltip {
        line-height: 16px;
        box-shadow: 0 0 10px $shadow-gray;
        position: absolute;
        z-index: 1000;
        background-color: $white;
        color: $black-color;
        margin: auto;
        transform: translate(-50%, 0);
        top: 100%;
        left: 20px;
        padding: 10px;
        border-radius: 3px;
        border-width: 1px;
        border-style: solid;
        border-color: $light-gray;
        border-image: initial;
        visibility: hidden;
        opacity: 0;
        transition: all 0.25s ease 0s;
        transition-delay: 0s;
        max-width: 250px;
        width: max-content;
        font-size: 13px;
        p {
            margin-bottom: 12px;
        }
        .saa-color-gray,
        .a-color-secondary,
        .saa-color-secondary,
        .saa-color-red {
            font-size: 13px;
        }
    }
    .saa-tooltip {
        pointer-events: none;
    }
    .saa-show-tooltip {
        max-width: fit-content;
    }
    .saa-tooltip-box:hover .saa-tooltip {
        visibility: visible;
        opacity: 1;
        transition: 0.25s;
        transition-delay: 0.5s;
    }

    .saa-highlight-green {
        margin: 0 0.2rem;
        padding: 0.2rem;
        background: $highlight-green;
        border-radius: 5px;
    }
    .saa-highlight-red {
        margin: 0 0.2rem;
        padding: 0.2rem;
        background: $highlight-red;
        border-radius: 5px;
    }
    .saa-highlight-blue {
        margin: 0rem 0.2rem;
        padding: 0.2rem;
        background: $highlight-blue;
        border-radius: 5px;
    }
    i.icon-down-open-mini::before {
        margin-left: -0.2rem;
        margin-right: -0.2rem;
    }

    .saa-calc-block {
        .switch {
            position: relative;
            display: inline-block;
            width: 26px;
            height: 11px;
            margin-left: 5px;
        }
        .switch input { 
            opacity: 0;
            width: 0;
            height: 0;
        }
        .slider {
            position: absolute;
            cursor: pointer;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #ccc;
            -webkit-transition: .4s;
            transition: .4s;
        }
        .slider:before {
            position: absolute;
            content: "";
            height: 10px;
            width: 10px;
            left: 1px;
            top: 0.5px;
            background-color: $white;
            -webkit-transition: .4s;
            transition: .4s;
        }
        input:checked + .slider {
            background-color: $dark-blue;
        }
        input:focus + .slider {
            box-shadow: 0 0 1px $dark-blue;
        }
        input:checked + .slider:before {
            -webkit-transform: translateX(14px);
            -ms-transform: translateX(14px);
            transform: translateX(14px);
        }
        .slider.round {
            border-radius: 10px;
        }
        .slider.round:before {
            border-radius: 50%;
        }
        .currency-symbol {
            right: 10px;
            position: absolute;
            top: 5px;
        }
        .saa-dropdown {
            &.table {
                width: 350px;
            }
            &.saa-dropdown-vat {
                width: 150px;
                margin-left: -76px;
            }
            .price-line {
                margin-bottom: 4px;
                font-size: 13px;
                .saa-tooltip {
                    font-size: 12px;
                }
                .price-dynamic {
                    display: flex;
                    align-items: center;
                }
                display: flex;
                justify-content: space-between;
            }
            table {
                font-size: 12px;
                color: $dark-gray;
                text-align: center;
                line-height: 15px;
                th {
                    text-align: center;
                }
                th:not(:last-child), td:not(:last-child) {
                    border-right: 1px solid $light-gray;
                }
                thead {
                    tr:first-child {
                        border-bottom: 1px solid $light-gray;
                    }
                    th {
                        font-weight: 400;
                        padding-bottom: 2px;
                    }
                }
                tbody {
                    td:not(:first-child) {
                        color: $dark-blue;
                    }
                    tr:first-child td {
                        padding-top: 2px;
                    }
                    tr:last-child td {
                        padding-bottom: 2px;
                    }
                    td:not(:first-child):hover {
                        background: $white-background;
                        cursor: pointer;
                        color: $orange;
                    }
                    .selected {
                        background: $white-background;
                        color: $orange !important;
                    }
                }
            }
        }
        .saa-dropdown { 
            margin-left: -175px;
            top: 26px;
        }
    }

    .saa-dropdown-box {
        border-radius: 50%;
        position: relative;
    }
    .saa-dropdown {
        position: absolute;
        z-index: 1000;
        background-color: $white;
        width: 200px;
        margin-left: -150px;
        top: 20px;
        left: 50%;
        padding: 10px;
        border-radius: 3px;
        border: 1px solid $light-gray;
        visibility: hidden;
        opacity: 0;
        transition: 0.2s;
    }
    .saa-tooltip-container {
        display: inline;
    }

    .saa-show-tooltip-update {
        visibility: visible;
        opacity: 1;
        transition: 0.2s;
    }
    .saa-dropdown {
        width: 200px;
    }

    .saa-dropdown::before {
        border-width: 1px;
        border-style: solid;
        border-color: $light-gray transparent transparent $light-gray;
        background-color: $white;
        content: " ";
        position: absolute;
        width: 10px;
        height: 10px;
        z-index: 999;
        top: 11px;
        left: 50%;
        transform: translate(-50%, calc(-100% - 5px)) rotate(45deg);
    }

    .saa-dropdown-item {
        text-align: center;
    }
    .saa-dropdown-item:hover {
        background-color: $white;
        margin: 0 -10px;
    }

    .isOpen {
        visibility: visible;
        opacity: 1;
    }

    .saa-circle {
        border-radius: 50%;
    }

    .saa-no-underline {
        text-decoration: unset;
        &:hover {
            text-decoration: unset;
        }
    }
    .saa-nowrap {
        white-space: nowrap;
    }
    .saa-copy-icon {
        i {
            margin: 0 -0.2rem;
        }
    }
    .saa-error {
        background-color: $error-red;
        padding: 8px;

        .saa-error-header-block {
            display: flex;
            justify-content: space-between;
        }

        hr {
            width: 100%;
            margin-top: 5px;
            margin-bottom: 10px;
        }

        .saa-btn-align {
            text-align: -webkit-center;
        }

        .saa-error-body {
            display: flex;
            flex-direction: column;
            font-size: 12px;
            span {
                margin-right: auto;
            }
        }
        .saa-cta-btn {
            box-sizing: border-box;
            background-color: #FFD814;
            display: flex;
            -webkit-box-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            align-items: center;
            border: 1px transparent;
            border-radius: 7px;
            transition: border-color 0.4s ease, transform 0.4s ease, box-shadow 0.4s ease, -webkit-transform 0.4s ease;
            padding: 8px 16px;
            font-size: 12px;
            max-width: 200px;
            width: 120px;
            color: $cta-text-blue !important;
            align-self: center;
            font-weight: 400;
            line-height: 16px;
            letter-spacing: 0em;
            text-align: center;
            text-decoration: none;
            &:hover {
                background-color: #F7CA00;
            }
            &:active {
                background-color: #F4B135;
            }
        }
    }
    .fa-search {
        margin-right: 2px;
    }
    .fa-angle-down {
        color: $dark-blue;
    }
    a:hover {
        .fa-angle-down {
            color: $orange;
        }
    }
    .a-size-base {
        font-size: 13px !important;
    }
    a:hover {
        color: $orange;
    }
    .saa-dropdown-search {
        width: 135px;
        margin-left: -65px;
    }
    .saa-list-without-style > li {
        list-style-type: none;
        margin-left: 0;
        margin-top: 6px;
        span {
            display: inline-block;
            margin-bottom: 0 !important;
        }
        span:not(:first-child) {
            margin-left: 15px;
        }
    }
    .sk-fading-circle {
        margin: 100px auto;
        width: 40px;
        height: 40px;
        position: relative;
    }

    .sk-fading-circle .sk-circle {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }

    .sk-fading-circle .sk-circle:before {
        content: "";
        display: block;
        margin: 0 auto;
        width: 15%;
        height: 15%;
        background-color: $gray;
        border-radius: 100%;
        -webkit-animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
        animation: sk-circleFadeDelay 1.2s infinite ease-in-out both;
    }
    .sk-fading-circle .sk-circle2 {
        -webkit-transform: rotate(30deg);
        -ms-transform: rotate(30deg);
        transform: rotate(30deg);
    }
    .sk-fading-circle .sk-circle3 {
        -webkit-transform: rotate(60deg);
        -ms-transform: rotate(60deg);
        transform: rotate(60deg);
    }
    .sk-fading-circle .sk-circle4 {
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        transform: rotate(90deg);
    }
    .sk-fading-circle .sk-circle5 {
        -webkit-transform: rotate(120deg);
        -ms-transform: rotate(120deg);
        transform: rotate(120deg);
    }
    .sk-fading-circle .sk-circle6 {
        -webkit-transform: rotate(150deg);
        -ms-transform: rotate(150deg);
        transform: rotate(150deg);
    }
    .sk-fading-circle .sk-circle7 {
        -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        transform: rotate(180deg);
    }
    .sk-fading-circle .sk-circle8 {
        -webkit-transform: rotate(210deg);
        -ms-transform: rotate(210deg);
        transform: rotate(210deg);
    }
    .sk-fading-circle .sk-circle9 {
        -webkit-transform: rotate(240deg);
        -ms-transform: rotate(240deg);
        transform: rotate(240deg);
    }
    .sk-fading-circle .sk-circle10 {
        -webkit-transform: rotate(270deg);
        -ms-transform: rotate(270deg);
        transform: rotate(270deg);
    }
    .sk-fading-circle .sk-circle11 {
        -webkit-transform: rotate(300deg);
        -ms-transform: rotate(300deg);
        transform: rotate(300deg);
    }
    .sk-fading-circle .sk-circle12 {
        -webkit-transform: rotate(330deg);
        -ms-transform: rotate(330deg);
        transform: rotate(330deg);
    }
    .sk-fading-circle .sk-circle2:before {
        -webkit-animation-delay: -1.1s;
        animation-delay: -1.1s;
    }
    .sk-fading-circle .sk-circle3:before {
        -webkit-animation-delay: -1s;
        animation-delay: -1s;
    }
    .sk-fading-circle .sk-circle4:before {
        -webkit-animation-delay: -0.9s;
        animation-delay: -0.9s;
    }
    .sk-fading-circle .sk-circle5:before {
        -webkit-animation-delay: -0.8s;
        animation-delay: -0.8s;
    }
    .sk-fading-circle .sk-circle6:before {
        -webkit-animation-delay: -0.7s;
        animation-delay: -0.7s;
    }
    .sk-fading-circle .sk-circle7:before {
        -webkit-animation-delay: -0.6s;
        animation-delay: -0.6s;
    }
    .sk-fading-circle .sk-circle8:before {
        -webkit-animation-delay: -0.5s;
        animation-delay: -0.5s;
    }
    .sk-fading-circle .sk-circle9:before {
        -webkit-animation-delay: -0.4s;
        animation-delay: -0.4s;
    }
    .sk-fading-circle .sk-circle10:before {
        -webkit-animation-delay: -0.3s;
        animation-delay: -0.3s;
    }
    .sk-fading-circle .sk-circle11:before {
        -webkit-animation-delay: -0.2s;
        animation-delay: -0.2s;
    }
    .sk-fading-circle .sk-circle12:before {
        -webkit-animation-delay: -0.1s;
        animation-delay: -0.1s;
    }

    @-webkit-keyframes sk-circleFadeDelay {
        0%,
        39%,
        100% {
            opacity: 0;
        }
        40% {
            opacity: 1;
        }
    }

    @keyframes sk-circleFadeDelay {
        0%,
        39%,
        100% {
            opacity: 0;
        }
        40% {
            opacity: 1;
        }
    }
}
