.saa-header {
    .saa-tooltip-container.saa-header-oversize {
        .saa-show-tooltip {
            max-width: 160px;
        }
    }
    line-height: 16px;
    .saa-block {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .header-links {
        line-height: 22px;
        .saa-header-offers {
            .saa-pointer {
                margin-left: 2px;
            }

            .saa-dropdown-parent-asin {
                margin-left: -363px;
                width: 710px;
            }
            div.saa-dropdown > li {
                display: flex;
                justify-content: space-between;
                align-items: center;
            }
            thead {
                th {
                    padding: 0.4em;
                    padding-right: unset;
                    &.sortable {
                        padding-left: 1.5em;
                    }
                    button {
                        &::before,
                        &::after {
                            left: 6px;
                        }
                    }
                }
            }
        }
        .saa-ebay-listings {
            li {
                list-style-type: none;
                margin: 0.3rem 0;
            }

            .saa-dropdown-ebay {
                margin-left: -75px;
                width: 150px;
                left: 35px;
                top: 22px;
            }
        }
    }

    .saa-notify-icons {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        flex-grow: 1;
        padding-top: 2px;
        .saa-tooltip-box {
            padding-top: 0px;
        }

        span {
            margin-left: 5px;
        }
        .saa-small-and-light,
        .saa-hazmat,
        .saa-meltable,
        .saa-header-gate {
            a {
                @extend %icons-border-radius-and-transition;
            }
        }
        .saa-is-adult {
            margin-top: 2px;
        }
        .saa-meltable {
            padding-top: 1px;
        }
        .saa-hazmat {
            svg {
                height: 12px;
                @extend %icons-border-radius-and-transition;
            }
            .saa-tooltip {
                :last-child {
                    margin: 12px 0 0 0;
                }
            }
        }
        .saa-fragile,
        .saa-header-amz {
            padding-top: 1px;
            i {
                @extend %icons-border-radius-and-transition;
            }
        }
        .saa-header-amz {
            svg {
                font-size: 15px;
            }
        }
        .saa-header-ip {
            padding-top: 1px;
            .ip-tooltip {
                width: 400px;
            }
            .saa-tooltip {
                max-width: 360px;
                div ul {
                    margin-left: 0;
                }
                div span {
                    margin-bottom: 15px;
                }
                .saa-line {
                    display: flex;
                    align-items: center;
                    line-height: 25px;
                }
                .saa-brand {
                    margin-bottom: 3px !important;
                }
            }
            .saa-tooltip-box:hover .ip-tooltip {
                margin-left: -200px;
            }
            .saa-badge {
                background-color: #f7d5d5;
                color: #b12704;
                padding: 0 8px;
                margin-left: 10px;
                font-size: 10px;
                border-radius: 10px;
                font-weight: 700;
            }
        }
        .saa-header-dislike {
            .saa-dislike {
                vertical-align: bottom;
            }
        }
        .saa-header-like {
            .saa-circle {
                margin-bottom: 1px;
            }
        }
        .saa-tooltip {
            span {
                margin-left: 0;
            }
        }
        .saa-header-gate {
            padding-top: 1px;
            span {
                margin-left: 0;
            }
            .saa-need-approval {
                width: 300px;
            }

            .saa-data-source {
                font-size: 10px;
                line-height: 125%;
                color: #565959;
            }
        }
    }

    .saa-dropdown-button {
        height: 21px;
        cursor: pointer;
        display: flex;
        .saa-tooltip-box {
            display: flex;
            align-items: center;
            margin: 0 8px 0 0;
            cursor: pointer;
        }
        .saa-tooltip {
            font-size: 12px;
            line-height: 15px;
            color: $black-color;
        }
        svg {
            margin: auto;
            path {
                opacity: 1;
                fill: $dark-blue !important;
            }
        }
        svg:hover path {
            fill: $orange !important;
        }
        path {
            transform: rotateX(180deg);
            transform-origin: center;
        }
        .close {
            transform: rotateX(0deg);
        }
        .saa-tooltip {
            width: max-content !important;
            margin: 12px 0 !important;
        }
    }

    .saa-snippets {
        display: flex;
        .saa-bsr-label {
            margin-left: 4px;
            font-weight: bold;
            font-size: 13px;
            line-height: 16px;
            letter-spacing: -0.01em;
            color: $black-color;
            padding: 1px 0;
        }
        .saa-snippet {
            margin: 0 4px;
            padding: 1px 4px;
        }
        .saa-bsr {
            font-weight: bold;
            font-size: 13px;
            border-radius: 5px;
            line-height: 18px;
            padding: 1.6px 4px;
        }
        .saa-green {
            color: $green;
            background: $transparent-green;
        }
        .saa-red {
            color: $red;
            background: $transparent-red;
        }
        .saa-blue {
            color: $blue;
            background: $transparent-blue;
        }
        .saa-fbm {
            @include snippets-box($white, $blue);
        }
        .saa-fba {
            @include snippets-box($white, $orange);
        }
        .saa-bsr-block {
            display: flex;
            align-items: center;
        }
        .saa-tooltip-box {
            cursor: default;
        }
        .saa-tooltip {
            font-size: 12px;
            line-height: 15px;
            color: $black-color;
            width: max-content !important;
            margin: 12px 0 !important;
        }
        .saa-tooltip span.saa-bsr-span {
            font-weight: bold;
            font-size: 13px;
            line-height: 16px;
            color: $black-color;
        }
        .saa-tooltip span.saa-category {
            color: $dark-blue;
        }
    }
    .fa-thumbs-down {
        // margin-bottom: -2px !important;
    }
}
