                                    </p>
                                    <p v-if="tour.currentStep == 4">
                                        {{ $t('Here you can see the characteristics of the product and basic information about it') }}. <br/>{{ $t('Using UPC, EAN and Model you can easily find the product you need on Google, Google Shopping, eBay, Walmart ,Target, Webstaurant, Katom, The Home Depot, Alibaba') }}. <br/>{{ $t('These help you to find the most profitable supplier of the product for further sale on Amazon') }}.
                                    </p>
                                    <p v-if="tour.currentStep == 5">
                                        {{ $t('Here you can see a hypothetical estimate of the possible number of sales of the product') }}. <br/><span class="mt-2 d-block">{{ $t('More about this') }} <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_80fd7765f4" target="_blank">{{ $t('here') }}.</a></span>
                                    </p>
                                    <p v-if="tour.currentStep == 6">
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_03598bf542" target="_blank">Price</a> - {{ $t('the extension shows the BuyBox price. If the product doesn’t have BuyBox, then the minimum price including delivery is shown') }}. <br/><a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_03598bf542">COG</a> - {{ $t('Cost of Goods. Here you can enter the cost of goods from the supplier, excluding or including taxes') }}.
                                    </p>
                                    <p v-if="tour.currentStep == 7">
                                        {{ $t('Here is the counting results panel of FBM/FBA') }}: <br/><a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_51a481c040" target="_blank">FBM</a> - {{ $t('fulfillment by merchant') }}. <br/><a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_51a481c040" target="_blank">FBA</a> - {{ $t('fulfillment by Amazon. If the number is green, then the transaction will be profitable; If it is red - the transaction will be unprofitable') }}
                                    </p>
                                    <p v-if="tour.currentStep == 8">
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">FBA / FBM switch</a> - {{ $t('switch that changes the display of FBA / FBM costs') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">Fees</a> - {{ $t('The total amount of Amazon commissions') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">R15%</a> - {{ $t('Amazon Referral Fee. If VAT is included, it will be displayed here') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">FBA</a> - {{ $t('FBA Commission. Available if Amazon knows the size of the item and its weight') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">Sto</a> - {{ $t('The cost of monthly storage of the goods in Amazon warehouses. Available for the USA only') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">3PL</a> - {{ $t('Third-party logistics. Total logistics costs (Ship + Prep + Frgl)') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">Ship</a> - {{ $t('The cost of shipping') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">Prep</a> - {{ $t('The cost of prep center services') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_bd77b21721" target="_blank">Frgl</a> - {{ $t('The cost of additional packaging of goods in a bubble wrap') }}.
                                    </p>
                                    <p v-if="tour.currentStep == 9">
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_150feb5f9e" target="_blank">{{ $t('Side panel view') }}</a> {{ $t('displays the most information about the product on side panel, such as name, ASIN, brand, alerts, number of sellers, BSR and profit calculator') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_150feb5f9e" target="_blank">{{ $t('Keepa chart') }}</a> {{ $t('it display historical Keepa chart') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_150feb5f9e" target="_blank">{{ $t('Settings') }}</a> {{ $t('it helps to open the settings page in your personal account') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_150feb5f9e" target="_blank">{{ $t('Source link') }}</a> - {{ $t('a supplier link input field. Available if the source link variable is selected for export in the extension settings') }}. <br/>
                                        <a href="https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_150feb5f9e" target="_blank">{{ $t('Notes') }}</a> - {{ $t('field for entering notes') }}. <br/>
                                        <span
                                            class="mt-2 d-block"
                                            v-html="
                                                $t('If the deal seems profitable to you, you can {0}save{1} it to your Google sheet', [
                                                    `<a href='https://help.sellerassistant.app/en/articles/7050072-seller-assistant-app-on-amazon-product-pages#h_150feb5f9e' target='_blank'>`,
                                                    '</a>',
                                                ])
                                            "
                                        ></span>
                                    </p>
                                    <p v-else-if="tour.steps[tour.currentStep].content.text">{{ $t(tour.steps[tour.currentStep].content.text) }}</p>
                                </div>
                            </div>
                        </div>
                        <div slot="actions" class="d-flex justify-end v-step__buttons" :class="{'justify-space-between': !tour.isFirst}">
                            <button v-if="tour.currentStep !== 0" @click="tour.previousStep" class="v-step__button v-step__button-previous">{{ $t("Back") }}</button>
                            <button v-if="tour.currentStep == 10" @click="finishOnBoarding" class="v-step__button v-step__button-next">{{ $t("Finish") }}</button>
                            <button v-else @click="tour.nextStep" class="v-step__button v-step__button-next">{{ $t("Next") }}</button>
                        </div>
                    </template>
                </v-step>
            </transition>
        </template>
    </v-tour>
</template>
<script>
import { mapGetters } from "vuex"
import { amplitude, updateOnboardingFinish } from "../../helpers"
import ClickOutside from "vue-click-outside"

export default {
    name: "OnBoarding",
    computed: mapGetters(["extConfig"]),
    directives: {
        ClickOutside,
    },
    mounted() {
        if(this.$store.state.user.onboarding && this.$tours.onBoarding) {
            if(this.extConfig.extensionCollapsed) {
                this.$store.commit("setExtConfig", {
                    extensionCollapsed: false,
                })
                setTimeout(() => {
                    this.$tours.onBoarding.start()
                }, 400)
            } else {
                this.$tours.onBoarding.start()
            }
        }
    },
    data() {
        return {
            starSVG: chrome.runtime.getURL("images/star.svg"),
            onBoardingCallbacks: {
                onSkip: () => this.resetOnBoarding()
            },
            steps: [
                {
                    target: '#v-step-start',
                    content: {
                        title: 'Welcome to the Seller Assistant App Powerful Amazon Product Research Tool',
                        text: 'Buckle up! A fascinating introduction to a tool that will simplify your research awaits for you.',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.resetStepsExceptOne("v-step-start", " saa-onboarding-overlay")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-0',
                    content: {
                        title: 'Link Panel'
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-0", " saa-onboarding-target")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-1',
                    content: {
                        title: 'Notification Panel'
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-1", " saa-onboarding-target pt-1 pl-1")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-2',
                    content: {
                        title: 'General Information from Amazon Catalog',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-2", " saa-onboarding-target")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-3',
                    content: {
                        title: 'Product Information & its characteristics',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-3", " saa-onboarding-target")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-4',
                    content: {
                        title: 'BuyBox Seller Information, Competition, and Potential Sales',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-4", " saa-onboarding-target")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-5',
                    content: {
                        title: 'Calculator',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-5", " saa-onboarding-target")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-6',
                    content: {
                        title: 'Profit Calculation',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-6", " saa-onboarding-target")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-7',
                    content: {
                        title: 'Fees and logistics costs',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-7", " saa-onboarding-target")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-8',
                    content: {
                        title: 'Settings panel and saving to google sheet',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        this.go("v-step-8", " saa-onboarding-target p-5")
                        resolve('next')
                    })
                },
                {
                    target: '#v-step-finish',
                    content: {
                        title: 'Well done!',
                        text: 'Now, you are ready to start making money!',
                    },
                    params: {
                        enableScrolling: false
                    },
                    before: type => new Promise((resolve, reject) => {
                        updateOnboardingFinish()
                        amplitude("OnBoarding Finish")
                        this.resetStepsExceptOne("v-step-finish", " saa-onboarding-overlay")
                        resolve('next')
                    })
                }
            ]
        };
    },
    methods: {
        finishOnBoarding() {
            this.$tours.onBoarding.finish()
            this.resetOnBoarding()
        },
        // handleOutside(e) {
        //     if(this.finished && e.target.localName != 'button') {
        //         this.$tours.onBoarding.finish()
        //         this.resetStepsExceptOne()
        //         var zoomWindow = document.getElementById("zoomWindow")
        //         if(zoomWindow) zoomWindow.style['z-index'] = '199'
        //     }
        // },
        resetOnBoarding() {
            //Completed if click the cross
            updateOnboardingFinish()

            this.resetStepsExceptOne()
            var zoomWindow = document.getElementById("zoomWindow")
            if(zoomWindow) zoomWindow.style['z-index'] = '199'
        },
        go(id, focusClasses = "") {
            if(this.extConfig.extensionCollapsed) {
                this.$store.commit("setExtConfig", {
                    extensionCollapsed: false,
                })
                setTimeout(() => {
                    this.resetStepsExceptOne(id, focusClasses)
                }, 400)
            } else {
                this.resetStepsExceptOne(id, focusClasses)
            }
        },
        resetStepsExceptOne(idStep = null, focusClasses = "") {
            var zoomWindow = document.getElementById("zoomWindow")
            if(zoomWindow) zoomWindow.style['z-index'] = '-1'
            //2, because finish and start (skip)
            for (let i = 0; i < (this.steps.length - 2); i++) {
                if(idStep != "v-step-" + i) {
                    if(("v-step-" + i) == "v-step-0") {
                        document.getElementById("v-step-" + i).className = "d-inherit"
                    } else if(("v-step-" + i) == "v-step-2") {
                        document.getElementById("v-step-" + i).className = "saa-pi-container saa-product-info-left"
                    } else if(("v-step-" + i) == "v-step-5") {
                        document.getElementById("v-step-" + i).className = "saa-block saa-calc"
                    } else if(("v-step-" + i) == "v-step-6") {
                        document.getElementById("v-step-" + i).className = "saa-profit-row"
                    } else if (("v-step-" + i) == "v-step-7") {
                        document.getElementById("v-step-" + i).className = "saa-background"
                    } else if(("v-step-" + i) == "v-step-8") {
                        document.getElementById("v-step-" + i).className = "saa-block saa-footer"
                    } else {
                        document.getElementById("v-step-" + i).className = ""
                    }
                } else {
                    document.getElementById("v-step-" + i).className += focusClasses
                }
            }
            document.getElementById("v-step-start").className = ""
            document.getElementById("v-step-finish").className = ""
            if(idStep == "v-step-start") document.getElementById("v-step-start").className += focusClasses
            if(idStep == "v-step-finish") document.getElementById("v-step-finish").className += focusClasses
        }
    }
}
</script>
<style scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>